.form {
  text-align: center;
  margin-bottom: 2rem;
  /* display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; */
}

.form button {
  font: inherit;
  cursor: pointer;
  background-color: #8a2b06;
  border: 1px solid #8a2b06;
  color: white;
  padding: 0.25rem 2rem;
  margin-top: 20px;

  border-radius: 20px;
  font-weight: bold;
}

.form button:hover,
.form button:active {
  background-color: #641e03;
  border-color: #641e03;
}